
.form label{font-weight:bold;color:#000
}
.form .field:not(:last-child){margin-bottom:.43em
}
.form .field__error{color:#fff;font-weight:bold;font-size:0.9rem
}
.form input,.form textarea{font-family:inherit;font-weight:bold;font-style:italic;border:none;outline:none;border-radius:5px;font-size:1rem;color:#fff;padding:0.4em 1em;margin:0;background-color:#281427;width:100%;max-height:0;overflow:hidden;transition:max-height 0.5s;max-height:300px
}
.form input::-webkit-input-placeholder,.form textarea::-webkit-input-placeholder{color:#f1c6e3
}
.form input:-moz-placeholder,.form textarea:-moz-placeholder{color:#f1c6e3
}
.form input::-moz-placeholder,.form textarea::-moz-placeholder{color:#f1c6e3
}
.form input:-ms-input-placeholder,.form textarea:-ms-input-placeholder{color:#f1c6e3
}
.inputs{position:relative;margin-bottom:2px
}
.inputs::after{content:'';display:block;margin:0 auto;clear:both;position:relative;top:-6px;left:0;right:0;width:0;height:0;border-left:8px solid transparent;border-right:8px solid transparent;border-top:10px solid #281427
}
.error{font-weight:bold;color:#fff;margin-bottom:1rem
}
.submit{border-radius:2em;background-color:#000;box-shadow:0 0 30px 0 rgba(189,43,235,0.5);outline:none;border:none;color:#fff;padding:0.5em 1em;width:100%;font-size:1.2rem;font-family:inherit;font-weight:bold;font-style:italic;margin-bottom:1.5em
}
.submit:hover{cursor:pointer
}



