.search input{background-color:transparent;border:none;outline:none;-ms-flex-positive:1;flex-grow:1;width:100%;padding:0.6em 1.0em 0.6em 0.5em;width:100%;color:#fff;font-weight:bold;font-style:italic;font-size:1.2em;font-family:inherit
}



