
.body--admin{background:#281427
}
.body--admin .logo{color:#fff
}
.body--admin .extension--published{background-color:#fff;color:#535353;z-index:1;background:linear-gradient(135deg, #ffb948, #ff45ad, #a723ff)
}
.body--admin .extension--published::before{position:absolute;top:5px;left:5px;width:calc(100% - 10px);height:calc(100% - 10px);border-radius:8px;content:'';z-index:-1;background:#fff;border:10px solid transparent;box-sizing:border-box
}
.body--admin .nav__menu li a:hover,.body--admin .nav__menu li a.active{color:#ff45ad;position:relative;display:inline-block
}
.body--admin .submit{background:linear-gradient(135deg, #ffb948, #ff45ad, #a723ff);box-shadow:0 0 20px rgba(0,0,0,0.3)
}



